/* Navbar container */
.navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(86, 79, 185);
  padding: 5px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  color: white;
}
/* Anhor tag of Navbar */
a {
  text-decoration: none;
  color: white;
}
